import axios from "axios";
export default {
    getWeibo(pageIndex=1){
        return (dispatch,getState)=>{
            axios.get("/weibo?"+"pageIndex="+pageIndex)
                .then(data=>{
                    dispatch({
                        type:"GET_DATA",
                        payload:{
                            data
                        }
                    })
                })
        }
    },

    addWeibo(context){
         console.log(context.length)
        return ()=>{
             if(context.length > 0){
                 axios.post("/weibo",{
                     context
                 }).then(
                     data=>{



                         this.getWeibo();
                         console.log(context)

                     }
                 )
             }else {
                 alert("请您输入正确的内容")
             }

        }
    },
    //删除weibo
    deleteWeibo(id){
        return ()=>{
            console.log(id)
            axios.delete("/weibo",{
                params:{
                    id
                }
            }).then(data=>{
                this.getWeibo();
            })
        }

    },
    //微博的顶与踩
    upWeibo(id,type){
        return ()=>{
            console.log(id)
            axios.put("/weibo",{
                id,
                type
            }).then(
                data=>{
                    this.getWeibo()
                }
            )
        }
    }
}