import React,{Component} from "react";
import TakeComment from "../Component/TakeComment";
import CommentOn from "../Component/CommentOn";
import  contextCreator from "../store/actionCreator/context"
import {
    connect
} from "react-redux"
import {
    bindActionCreators
} from "redux"
class Home extends Component{
    render() {
        return (
            <div className="jyArea">
                <TakeComment addWeibo={this.props.addWeibo.bind(this.props)}
                             getWeibo={this.props.getWeibo.bind(this.props)}
                ></TakeComment>
                <CommentOn contextList={this.props.contextList}
                           pageIndex={this.props.pageIndex}
                           pageSum={this.props.pageSum}
                           deleteWeibo={this.props.deleteWeibo.bind(this.props)}
                           upWeibo={this.props.upWeibo.bind(this.props)}
                           getWeibo={this.props.getWeibo.bind(this.props)}
                ></CommentOn>
            </div>
        );
    }
    componentDidMount() {


        this.props.getWeibo()
    }

}
function mapStateToProps(state){
    return{
        contextList:state.context.contextList,
        pageIndex:state.context.pageIndex,
        pageSum:state.context.pageSum
    }
}
export default connect(mapStateToProps,dispatch=>bindActionCreators(contextCreator,dispatch))(Home)


