import React,{Component} from "react";
import axios from "axios";

export default class TakeComment extends Component{
      render(){
          return(
              <div className="takeComment">
                  <textarea name="textarea" ref={ref=>this.context=ref} className="takeTextField" id="tijiaoText"></textarea>
                  <div className="takeSbmComment">
                      <input type="button" onClick={
                          // pubsub.publish("one",{
                          //     a:1,
                          //     b:2
                          // });
                          this.addWeibo1.bind(this)
                          // this.props.addWeibo(this.refs.context.value)
                      } className="inputs" value="" />
                  </div>
              </div>
          )

      }
    addWeibo1(){
        console.log(this.context.value)

            if(this.context.value.length > 0){
                axios.post("/weibo",{
                    context:this.context.value
                }).then(
                    data=>{


                        this.context.value="";
                        this.props.getWeibo();

                        console.log(this.context.value)

                    }
                )
            }else {
                alert("请您输入正确的内容")
            }
        //
    }

}
