import React from "react"
function PageInfo (props){
    // 逻辑
    var pageArr =[];
    for(let i=1;i<=props.pageSum;i++){
        pageArr.push(<a key={i} href="javascript:;"
                        onClick={()=>{
                            props.getWeibo(i);
                        }}
                        className={props.pageIndex==i?"active":""}>{i}</a>)
    }
    return (
        <div
            className="page"
            style={{display:props.contextList.length>0?"block":"none"}}
        >
            {pageArr}
        </div>
    )
}
export default PageInfo