import React,{Component} from "react";
import Page from "./page"
export default class CommentOn extends Component{
    render(){
        return(
            <div className="commentOn">
                <div className="noContent" style={{display:this.props.contextList.length>0?"none":"block"}}>暂无留言</div>
                <div className="messList">
                    {
                        this.props.contextList.map((v,i)=>{
                            return (
                                <div key={i} className="reply">
                                    <p className="replyContent">{v.context}</p>
                                    <p className="operation">
                                        <span className="replyTime">{v.addTime}</span>
                                        <span className="handle">
                                        <a href="javascript:;" className="top" onClick={()=>{
                                            this.props.upWeibo(v._id,1)}}>{v.topNum}</a>
                                        <a href="javascript:;" className="down_icon" onClick={()=>{
                                                this.props.upWeibo(v._id,2)}}>{v.downNum}</a>
                                        <a href="javascript:;" className="cut" onClick={()=>{
                                            this.props.deleteWeibo(v._id)
                                        }}>删除</a>
                                    </span>
                                    </p>
                                </div>
                            )
                        })
                    }

                </div>
               <Page
                   getWeibo={this.props.getWeibo}
              contextList={this.props.contextList}
                   pageIndex={this.props.pageIndex}
                   pageSum={this.props.pageSum}
               ></Page>
            </div>
        )

    }
    componentDidMount(){
        console.log(this.props.contextList)
    }
}